export default {
	data() {
		return {
			keywords: "",
			searchmove: [],
			num: 12
		}
	},
	onLoad() {
		uni.request({
			url: `${this.$url}/search/list?qq=122212489&keywords=${""}&page=1&pageSize=${this.num}`,
			method: 'POST',
			data: {},
			success: res => {
				this.searchmove = res.data.data.rows
				console.log(res.data.data.rows);
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
	},
	//上拉触底
	onReachBottom: function() {
		this.num +=12
		console.log(this.num);
		console.log(this.keywords);
		uni.request({
			url: `${this.$url}/search/list?qq=122212489&keywords=${this.keywords}&page=1&pageSize=${this.num}`,
			method: 'POST',
			data: {},
			success: res => {
				this.searchmove = res.data.data.rows
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
	},
	methods: {
		todetails(item) {
			let obj = JSON.stringify(item)
			uni.navigateTo({
				url: `../details/details?item=${obj}`
			})
		},
		inp(e) {
			this.keywords = e.detail.value
			this.num = 12
			uni.request({
				url: `${this.$url}/search/list?qq=122212489&keywords=${this.keywords}&page=1&pageSize=${this.num}`,
				method: 'POST',
				data: {},
				success: res => {
					this.searchmove = res.data.data.rows
					console.log(res.data.data.rows);
				},
				fail: (e) => {
					console.log(e);
				},
				complete: () => {}
			});
		}
	}
}
